.aboutContainer
{
    margin-top: 55px;
    margin-bottom: 55px;

}
.aboutTitle
            {
                color: rgb(41, 41, 41);
                font-size: 40px;
                font-weight: 500;
                text-shadow: 5px 5px 3px rgba(105, 105, 105, 0.37);
                display: flex; flex-direction: row; justify-content: center;
                box-sizing: border-box;
            }
            .aaa{box-sizing: border-box;}
            .aboutContent
            {
                color: rgba(100, 100, 100, 0.74);
                font-size: 15px;
                font-weight: 400;
                display: inline-block;
                margin-bottom: 40px;
   
            }
            .downloadBtn
            {
                color: white;
                background-color: rgb(58, 58, 58);
                border: 0px;
                border-radius: 2px;
                padding: 16px 26px;
            }
            .downloadBtn:focus
            { background-color: rgb(58, 58, 58);
                box-shadow: 0px 0px 0px !important;
            }
            .downloadBtn:hover
            {
                background-color: rgb(80, 80, 80);
            }
            .downloadBtn:active
            {background-color: rgb(0, 0, 0) !important;
                box-shadow: 0px 0px 0px !important;
            }