.cover
{flex-direction: row; align-items: center; background-color: black; display: flex; height: 550px; padding: 0px;}
.coverImg{position: absolute; }
.coverCard{text-align: center; width: 100%; margin: 0px;}
.name
{
    color: black;
    font-size: 40px;
    font-weight: 500;

}
.title
{
    color: black;
    font-size: 22px;
    font-weight: 400;
    display: inline-block;
    margin-bottom: 35px;
}
.contactBtn
{
    border: 2px solid black;
    color: black;
    border-color: black;
}
.contactBtn:hover
{
    color: rgb(255, 255, 255);
    border-color: rgb(56, 56, 56);
    background-color: rgb(56, 56, 56);
}
.contactBtn:active
{
    color: rgb(255, 255, 255) !important;
    border-color: black !important;
    background-color: black !important;
    box-shadow: 0px 0px 0px !important;
}
.contactBtn:focus
{

    box-shadow: 0px 0px 0px;
}